@import "../revealjs/css/theme/white.css";

$blue: #2298ef;

html.chapter-title {
	.reveal .slide-background {
		background-color: $blue;
	}

	section {
		color: white;
		font-weight: 700 !important;
		text-align: center;
	}
}

.reveal {

	.title-page {

        h1 {
          font-size: 1.6em;
          font-weight: 800;
          color: $blue;
        }

		text-align: left;
		padding-left: 20px;
		.author {
			font-size: .8em;
		}
	}

	.links {
		justify-content: space-between;
		position: fixed;
		bottom: 4em;
		left: 0;
		right: 0;
		display: flex;

		a {
			color: $blue;
			font-size: .8em;
		}

		img {
			border: none;
			box-shadow: none;
			margin: 0 .4em;
			background: none;
			width: 1.5em;
			height: 1.5em;
		}

	}

	section .disclaimer {
		font-size: .3em;
		position: fixed;
		bottom: 0;
		right: 0;
		font-style: italic;
	}

	.list-header {
		position: fixed;
		left: 0;
		right: 0;
		top: 2.5em;
	}


	.caption {
		position: fixed;
		bottom: 0;
		right: 0;
		color: #ccc !important;
		font-size: 0.35em !important;
	}

	.aside {
		font-size: .8em;
		font-style: italic;
	}

	pre {
		box-shadow: none;
		white-space: pre-wrap;
		width: 100%;
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	pre code {
		max-height: 100%;
		padding: 0 15px;
	}

	section div {
		line-height: 1.7em;
	}

	@for $i from 0 through 50 {

		section.scatter {
			//position:fixed;
			//left: 0;
			//right: 0;
			//top: 0 !important;
			//bottom: 0;
		}

		$float: left, right;
		$colors: #f3b878, #ad7ce4, #ff868b, #156680, #7cffd8;

		//section.scatter div:nth-of-type(#{$i}) {
		//	font-size: .7 + (random(100) / 100) + em;
		//	padding: .1em 0.5 + random(5) / 20 + em;
		//	float: nth($float, random(length($float)));
		//	color: nth($colors, random(length($colors)));
		//}
	}


}

.background-img body {
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center center;
	background-color: #222;
	color: #ddd;
}

div.arrow-shape-container {
	$arrowHeight: 1.5em;
	$arrowBarWidth: 4.2em;
	$arrowTriangleLipHeight: .25em;

	$arrowHeightLarge: 4em;
	$arrowBarWidthLarge: 7em;
	$arrowTriangleLipHeightLarge: .25em;

	$arrowHeightSmall: .4em;
	$arrowBarWidthSmall: 6em;
	$arrowTriangleLipHeightSmall: .15em;

	$arrowTriangleHeight: ($arrowHeight / 2) + ($arrowTriangleLipHeight * 2);
	$arrowTriangleHeightLarge: ($arrowHeightLarge / 2) + ($arrowTriangleLipHeightLarge * 2);
	$arrowTriangleHeightSmall: ($arrowHeightSmall / 2) + ($arrowTriangleLipHeightSmall * 2);
	$arrowColor: $blue;

	position: relative;


	div.arrow-shape {
		width: $arrowBarWidth;
		height: $arrowHeight;
		background-color: $arrowColor;
		margin: 0 $arrowTriangleHeight;
		display: flex;
        flex-direction: column;
		justify-content: space-around;
		align-self: center;

		div {
          	line-height: 1em;
			font-size: .8em;
		  	color: #fff;
        }
	}


	div.arrow-shape:not(.ltr)::before {
		content: "";
		position: absolute;
		left: -$arrowTriangleHeight;
		border: $arrowTriangleHeight solid transparent;
		border-right-color: $arrowColor;
	}

	div.arrow-shape:not(.rtl)::after {
		content: "";
		position: absolute;
		left: $arrowTriangleHeight + $arrowBarWidth;
		border: $arrowTriangleHeight solid transparent;
		border-left-color: $arrowColor;
	}


	&.large {

		div.arrow-shape {
			width: $arrowBarWidthLarge;
			height: $arrowHeightLarge;
			margin: 0 $arrowTriangleHeightLarge;

			div {
				font-size: .8em;
			}

		}


		div.arrow-shape::before {

			left: -$arrowTriangleHeightLarge;
			border-width: $arrowTriangleHeightLarge;
		}

		div.arrow-shape::after {

			left: $arrowTriangleHeightLarge + $arrowBarWidthLarge;
			border-width: $arrowTriangleHeightLarge;

		}

	}

	&.small {

		div.arrow-shape {
			width: $arrowBarWidthSmall;
			height: $arrowHeightSmall;
			margin: 0 $arrowTriangleHeightSmall;

			div {
				font-size: .8em;
			}

		}


		div.arrow-shape::before {

			left: -$arrowTriangleHeightSmall;
			border-width: $arrowTriangleHeightSmall;
		}

		div.arrow-shape::after {

			left: $arrowTriangleHeightSmall + $arrowBarWidthSmall;
			border-width: $arrowTriangleHeightSmall;

		}

	}

}

.template {
	display: none;
}


.fragment.fade-past:not(.current-fragment), .fade {
	color: #bbb !important;
}

.blue {
	color: $blue;
}

